@font-face {
    font-family: 'Caveat';
    src: url('font/Caveat-VariableFont_wght.ttf');
}

:root {
    transition: font-size 0.5s ease;
}

:root.light {
    --bg-base: #ECE3D4;
    --bg-footer: #E3D6C3;
    --border: #C7A26B;
    --title-font: #372A15;
    --title-sub-font: #5C4523;
    --main-text-title-font: #111827;
    --main-text-font: #374151;
    --hover-brightness: 0.9
}

:root.dark {
    --bg-base: #333333;
    --bg-footer: #E3D6C3;
    --border: #C7A26B;
    --title-font: #372A15;
    --title-sub-font: #5C4523;
    --main-text-title-font: #111827;
    --main-text-font: #374151
}

body {
    background-color: var(--bg-base);
    color: var(--bg-color5);
}

.bg-transition {
    transition: background-color 0.3s ease, color 0.3s ease;
}

/*데스크탑*/
@media all and (min-width:1279px) {
    html {
        font-size: 18px;
        /* 기본 폰트 사이즈를 18px로 설정 */
    }
}

/*노트북 & 태블릿 가로 : 1024px-1279px*/
@media all and (min-width:1024px) and (max-width:1279px) {
    html {
        font-size: 16px;
        /* 기본 폰트 사이즈를 16px로 설정 */
    }
}

/*태블릿 가로 : 769px-1023px*/
@media all and (min-width:768px) and (max-width:1023px) {
    html {
        font-size: 15px;
        /* 기본 폰트 사이즈를 15px로 설정 */
    }
}

/*모바일 가로 & 태블릿 세로 : 480px-767px*/
@media all and (min-width:480px) and (max-width:767px) {
    html {
        font-size: 14px;
        /* 기본 폰트 사이즈를 14px로 설정 */
    }
}

/*모바일 : -479px*/
@media all and (max-width:479px) {
    html {
        font-size: 13px;
        /* 기본 폰트 사이즈를 13px로 설정 */
    }
}

/* 가로 */
@media all and (orientation: landscape) {}

/* 세로 */
@media all and (orientation: portrait) {}

header {
    display: flex;
    border-bottom: 0.13rem solid var(--border);
    padding: 0.5rem 0rem;
    font-size: 2rem;
    font-family: 'Caveat';
    font-weight: 900;
}

footer {
    height: 2rem;
    background-color: var(--bg-footer);
    flex-shrink: 0;
}

.sidebar-open {
    width: 5rem;
}

.sidebar-close {
    display: none;
}

.sidebar-open-button {
    margin: 0rem 0.7rem;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 0.4rem;
    border: solid 0.17rem var(--border);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.sidebar-open-icon-first,
.sidebar-open-icon-middle,
.sidebar-open-icon-final {
    width: 100%;
    height: 0.35rem;
    background-color: var(--bg-footer);
    transition: all 0.3s ease;
    margin-right: auto;
}

.sidebar-open-icon-middle {
    margin: 0.2rem 0;
}

.sidebar-open-button:hover .sidebar-open-icon-middle {
    opacity: 0;
    transform: scale(0.5);
}

.sidebar-open-button:hover .sidebar-open-icon-first {
    transform: translateY(0.18rem) rotate(60deg);
    width: 1rem;
    height: 0.3rem;
}

.sidebar-open-button:hover .sidebar-open-icon-final {
    transform: translateY(-0.18rem) rotate(-60deg);
    width: 1rem;
}

.flex {
    display: flex;
}

.hober-able:hover {
    filter: brightness(var(--hover-brightness))
}

.click-able {
    cursor: pointer;
}